.MainSec3 {
    display: flex;
    align-items: center;
    justify-content: space-around;
    min-height: 90vh;
}
.Sec3Left {
    flex: 1;
    padding: 8vw;
}
.Sec3Head {
    font-weight: 700;
    font-size: 56px;
    line-height: 56px;
}
.Sec3Text {
    margin-bottom: 60px;
}
.Sec3Text > p{
    font-size: 26px;
    line-height: 60px;
    font-weight: 500;
    margin-bottom: 22px;
}
.Sec3btn {
    text-decoration: none;
    color: #222222;
    font-weight: 700;
    font-size: 32px;
    background: #F9C83A;
    border-radius: 35px;
    padding: 12px 40px 12px 40px;
}

.Sec3Right {
    flex: 1.2;
    display: flex;
    justify-content: center;
}

.lCard {
    flex: 1;
}
.rCard {
    flex: 1;
}

.space1 {
    height: 80px;
}
.midSpace {
    height: 30px;
}
@media (max-width: 1246px) {
    .Sec3Head {
        font-size: 4vw;
    }
    .Sec3Head{
        padding-top: 0px;
        margin-top: 0px;
        line-height: normal;
    }
    .Sec3Text > p {
        line-height: normal;
        font-size: 2vw;
    }
}
@media (max-width: 986px) {
    .MainSec3 {
        padding-top: 0px;
        flex-direction: column;
    }
}
@media (max-width: 596px) {
    .Sec3Right {
        flex-direction: column;
    }
}