.heroSection {
    position: relative;
    /* overflow-x: hidden; */
    margin: 0px;
    padding: 0px;
    /* background: black; */
    width: 100vw;
    max-width: 100vw;
    /* min-height: 85vh; */
}
.circleHero {
    z-index: -2;
    position: absolute;
    background: #FFD24D;
    height: 90vh;
    max-height: 64vw;
    aspect-ratio: 1/1;
    border-radius: 50%;
    top: -11.43vh;
    right: -19.3vh;
}
.glassImage {
    z-index: -1;
    top: 12vh;
    position: absolute;
    right: 0px;
    max-width: 55vw;
}
.heroHeading {
    color: #222222;
    margin: 0px;
    padding-top: 9vh;
    padding-left: 12vh;
    padding-bottom: 15px;
    /* font-family:   sans; */
    font-weight: 700;
    font-size: 85px;
    line-height: 92px;
    
}
.heroSubHeading {
    display: flex;
    align-items: center;
    padding-left: 12vh;
    color: #222222;
    margin: 0px;
}
.heroSubHeading > h2 {
    color: #222222;
    margin: 0px;
    padding: 0px;
    /* font-family:   sans; */
    font-weight: 600;
    font-size: 65px;
}
.Dot {
    margin-left: 18px;
    margin-right: 18px;
    height: 17px;
    width: 17px;
    background: #F9C83A;
    border-radius: 50%;
}
.space {
    height: 62px;
}
.heroButton {
    color: #000;
    margin: 12vh;
    padding: 18px;
    background: #FFD24D;
    border-radius: 28px;
    text-decoration: none;
    /* font-family:   sans; */
    font-size: 28px;
    font-weight: 700;
}

@media (max-width: 1025px) {
    .heroHeading {
        font-size: 62px;
        line-height: 76px;
    }
    .heroSubHeading > h2 {
        font-size: 42px;
    }
    .heroButton {
        font-size: 22px;
    }
    .heroSection {
        height: 500px;
    }
    .Dot {
        height: 1vw;
        width: 1vw;
        margin-left: 1.2vw;
        margin-right: 1.2vw;
    }
}
@media (max-width: 1025px) {
    .heroHeading {
        font-size: 7vw;
        line-height: 9vw;
    }
    .heroSubHeading > h2 {
        font-size: 4.6vw;
    }
    .heroButton {
        font-size: 2.8vw;
        padding: auto;
    }
}
@media (max-width: 1025px) {
    .heroSection {
        /* min-height: 550px; */
        min-height: fit-content;
    }
    .heroHeading {
        padding-left: 10vw;
        font-size: 5.5vw;
        line-height: 7vw;
    }
    .heroSubHeading > h2 {
        font-size: 3vw;
    }
    .heroButton {
        font-size: 2.3vw;
    }
    .glassImage {
        top: 4vh;
    }
    .heroSubHeading {
        padding-left: 10vw;
        
    }
    .heroButton {
        margin-left: 10vw;
        margin-top: 0px;
    }
    .space {
        height: 30px;
    }
}

@media (max-width: 882px) {
    .heroSection {
        height: 450px;
    }
    
}
@media (max-width: 714px) {
    .heroSection {
        height: 60vw;
    }  
}
@media (max-width: 410px) {
    .heroSection {
        height: 70vw;
    }  
}
@media (max-width: 310px) {
    .heroSection {
        height: 220px;
    }  
}